<template>
<div class="my_message-box">
     <Header>
          <van-icon  tag="router-link" to='/message' slot="first" name="arrow-left" info="" />
          <p slot="mid"><b>账户信息</b></p>
          <p slot="last"></p>
     </Header>

     <section class="my_message-con">
          <div>
               <ul class="portrait">
                    <li>
                         <p> 头像 </p>
                         <p>

                              <input type="file" ref="inp" @change="Person()" class="file"/>
                              <img :src="'//elm.cangdu.org/img/'+this.$store.state.photo" alt="" v-if="this.$store">
                              <img :src="this.userImg" alt="" v-else>
                              <van-icon name='arrow' />

                         </p>
                    </li>
                    <router-link to="#" tag="li">
                         <p> 用户名 </p>
                         <p>
                              <span>{{this.$store.state.log.username}}</span>
                              <van-icon name='arrow' />
                         </p>
                    </router-link>
                    <router-link to="/site" tag="li">
                         <p> 收获地址 </p>
                         <van-icon tag="p" name='arrow' />
                    </router-link>
               </ul>
               <section class="bindi">
                    <span>账号绑定</span>
               </section>
               <section @click="showPopup()" class="bindi-to">
                    <p class="iconfont icon-shouji">
                    <span>手机</span>
                    </p>
                    <van-icon tag="p" name="arrow" />
               </section>
               <section class="bindi">
                    <span>安全设置</span>
               </section>

               <section class="bindi-to">
                    <p>登录密码</p>
                    <p class="bindi-to-p">
                         <router-link tag="span" to="/">修改</router-link>
                    <van-icon name="arrow" />  
                    </p>     
               </section>
               <div>
               <van-button @click="showTrue()" type="primary" class="New-address tui">退出登录</van-button>
               <!-- 修改手机号弹出层 -->
               <van-popup v-model="show"  round get-container="body" class="logon-popup"
                    position="center" :style="{width:'55%',height: '32%' }">
                    <van-icon name="warning-o" :style="{fontSize:'0.9rem',color:'#f8cb86'}"/>

                    <p>请在手机APP中设置</p>

                    <van-button @click="showFals()" type="primary" :style="{width:'100%',height: '25%' }">确认</van-button>
               </van-popup>
                <!-- 退出登录弹出层 -->
               <van-popup v-model="hide"  round get-container="body" class="logon-popup logon-red"
                    position="center" :style="{width:'80%',height: '45%' }">
                    <van-icon name="warning-o" :style="{fontSize:'1.3rem',color:'#f8cb86'}"/>

                    <p>是否退出登录</p>
                    <p>
                         <van-button @click="showFalse()" type="primary" :style="{width:'1.4rem',background:'#c1c1c1',border:'none'}">再等等</van-button>
                         <van-button @click="locaDel()" type="warning" :style="{width:'1.4rem' }">退出登录</van-button>
                    </p>
               </van-popup>
               </div>
          </div>
     </section>

</div>
</template>

<script>
// import Header from '@/components/header.vue'
import Header from '@/components/header'
export default {
     name:'my_message-box',
    components:{
         Header,
    },
    data(){
         return{
            userImg:'', 
            username:'',
            show: false, //手机弹出层
            hide:false, 
         }
         
    },
     methods: {
          //修改手机弹出框
          showPopup() {
                this.show = true;
          },
          showFals(){
               this.show = false;
          },
          //退出弹出层
          showTrue(){
               this.hide = true;
          },
          showFalse(){
               this.hide = false;
          },
          locaDel(){
               this.axios. get(`http://elm.cangdu.org/img/default.jpg`).then((res)=>{

                    this.$store.commit('del',res.config.url)
                    this.$router.push('/')
               })
               
          },
          //头像修改
          Person(){
               let input=this.$refs.inp;     //1.获取真实dom事件
               //console.log(input.files[0])

                //2. 数据类型保存文件  formData
                let data=new FormData()
               data.append("file",input.files[0])//data类型，append("file",文件)上传具文件/图片

                //3.修改请求头 content-type类型:multipart/form-data
                let headers={headers:{"Content-Type":"multipart/form-data"}}

               this.axios.post(`http://elm.cangdu.org/eus/v1/users/${this.$store.state.log.user_id}/avatar`,data,

                headers).then((res)=>{
                     //console.log(res)

                     this.$store.commit('photo',res.data.image_path)
                })
              
          }
     },
    mounted() {
     //     console.log(this.$store.state.log)
           this.axios. get(`http://elm.cangdu.org/img/default.jpg`).then((res)=>{
               //  console.log(res)
                this.userImg=res.config.url;
            })
    },
}
</script>

<style lang="scss">
p,ul,li{
     margin: 0;padding: 0;
}
.my_message-box,body,html{
     width: 100%;height: 100%;
     margin: 0;padding: 0;
     display: flex;
     flex-direction:column; 
}
a{color: #fff;}
.my_message-con{
     overflow: auto;
     flex: 1;
     .portrait{
          li{
               display: flex;box-sizing: border-box;
               justify-content:space-between;align-items: center;
               p{padding: 0 0.154rem;}
               p:nth-of-type(1){
                    font-size: 0.26rem;line-height: 1.2rem;
               }  
               p:nth-of-type(2){
                     font-size: 0.35rem;line-height: 1.2rem;
                     color: #999;
               }
               border-bottom: 0.005rem solid #999; 
          }
          li:nth-of-type(1){
               height: 1.48rem;
             p{
                height: 1.2rem;
               img{
                    width: 0.96rem;height: 0.96rem;
                    border-radius: 50%;vertical-align: middle;
                    }
               }  
          }
          li:nth-of-type(n+2){
               height: 0.78rem;
          }
     }
}
.bindi{
     padding: 0.154rem;background: #f5f5f5;
     font-size: 0.24rem;
}
.bindi-to{
      display: flex;
      box-sizing: border-box;
     justify-content:space-between;align-items: center;
     padding: 0.154rem;font-size: 0.26rem;
     .icon-shouji{
          font-size: 0.26rem;color: #3399ff;
          span{color: #000;}
          }
     .bindi-to-p{
          display: flex;color: #999;
          align-items: center;
     }
}
.tui{
     font-size: 0.24rem;
     margin-left: 3%;
    margin-top:0.158rem; 
     height: 0.6rem;width:94%;
    font-weight:normal;
    background: #d8584a;
    margin-top:0.888rem; 
    border-radius: 0.04rem;
    border: none;
}
    
 .logon-popup{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
     align-items: center;
     >p{font-size: 0.22rem;}
 }   
.logon-red{
     justify-content: space-around;
     >p{font-size: 0.26rem;}
     >p:nth-of-type(2){
          width: 100%;
          display: flex;
          justify-content: space-around;
           align-items: center;
     }
          
     
}
.file{
     position: absolute;
     opacity: 0;
} 
</style>


